.mountains-wrapper {
  position: relative;
  width: 100%;

  left: 0px;
  height: 150px;
}

.mountain {
  position: absolute;
  left: 0px;
  bottom: 0px;
  -webkit-animation: appearing 5s linear, slide1 60s linear infinite;
          animation: appearing 5s linear, slide1 60s linear infinite;
}

.mountain img {
  width: 200%;
  height: 350px;
  -o-object-fit: cover;
     object-fit: cover;
}

@-webkit-keyframes slide1 {
  from {
    left: 0px;
  }
  to {
    left: -1200px;
  }
}

@keyframes slide1 {
  from {
    left: 0px;
  }
  to {
    left: -1200px;
  }
}

@-webkit-keyframes appearing {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes appearing {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}



